@import "variables";//Custom Variables

//Bootstrap Functions, Variables and Mixins
@import "../../node_modules/bootstrap/scss/functions";
@import "../../node_modules/bootstrap/scss/variables";
@import "../../node_modules/bootstrap/scss/mixins";


.profile-left{
  img{
    background-color: #edf0f4;
    border: solid 4px #f7f7f7;
  }
}
.profile-right{
  background-color: #f7f7f7;
  padding: 24px 24px 24px 33px;
  height: 100%;
}
.profile-rating{
  margin-bottom: 12px;
  .fa{
    color: $primary;
    font-size: 28px;
  }
}
.profile-title{
  font-size: 24px;
  color: #4a4a4a;
  margin-bottom: 2px;
}
.profile-subtitle{
  font-size: 16px;
  color: #4a4a4a;
  margin-bottom: 10px;
}
.profile-info{
  p{
    font-size: 15px;
    color: #4a4a4a;
    margin-bottom: 5px;
    &:last-child{
      margin-bottom: 0;
    }
  }
}
.content-container{
  background-color: #f7f7f7;
  padding-top: 21px;
  padding-bottom: 21px;
}
.card-profile-page{
  border: 0;
  border-radius: 0;
  .card-header {
    background-color: #f7f7f7;
    border-bottom: 0;
    &:first-child {
      border-radius: 0;
    }
  }
  .card-body{
    background-color: #f7f7f7;
  }
  .card-footer {
    background-color: #f7f7f7;
    border-top: 2px solid #b7b7b8;
    padding: 20px 1.25rem;
    &:last-child {
      border-radius: 0;
    }
  }
}
.table-bookings{
  thead th{
    border-top: 0;
    font-family: $gothic;
    font-size: 16px;
    font-weight: normal;
    color: #4a4a4a;
    vertical-align: bottom;
    border-bottom: 3px dotted rgba(151, 151, 151, 0.65);
    padding-bottom: 2px;
    &:first-child{
      padding-left: 0;
    }
  }
  tbody{
    tr{
      td {
        font-size: 16px;
        font-weight: bold;
        color: #4a4a4a;
        border-top: 0;
        padding-top: 8px;
        padding-bottom: 0;
        &:first-child{
          padding-left: 0;
        }
      }
      &:first-child{
        td{
          padding-top: 24px;
        }
      }
    }
  }
}
.experience-content{
  border-bottom: 2px dotted rgba(151, 151, 151, 0.65);
  margin-bottom: 25px;
  &:last-child{
    border-bottom: 0;
    margin-bottom: 0;
  }
  p{
    font-size: 16px;
    color: #4a4a4a;
    margin-bottom: 5px;
  }
  .rating-star-sidebar{
    margin-top: 9px;
    margin-bottom: 8px;
    .fa{
      font-size: 20px;
      color: $primary;
    }
  }
}
.profile-service-list{
  padding-left: 18px;
  li{
    list-style: none;
  }
}
#calendar{
  //height: 900px;
}

.col-lg-3-reset{
  @include media-breakpoint-up(lg){
    flex: 0 0 27%;
    max-width: 27%;
  }
}
.col-lg-9-reset{
  @include media-breakpoint-up(lg){
    flex: 0 0 73%;
    max-width: 73%;
  }
}
.profile-left{
  text-align: center;
  img{
    //max-width: 270px;
    margin-bottom: 20px;
    @include media-breakpoint-up(lg){
      margin-bottom: 0;
    }
  }
}
.table-heading{
  font-size: 18px;
  color: #4a4a4a;
}
.table-action-btn{
  button{
    padding-top: 5px;
    padding-bottom: 5px;
    &:hover{
      cursor: pointer;
    }
  }
}


